form, figure {
  margin: 0; }

fieldset {
  margin: 0;
  padding: 0;
  border: 0; }

legend {
  display: none;
  border: 0;
  padding: 0;
  white-space: normal; }

button, input, select, textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
  border-radius: 0; }

button, input {
  line-height: normal; }

button, html input[type="button"], input[type="reset"], input[type="submit"] {
  -webkit-appearance: button;
